import React, {useState} from "react";

import { Container } from "@mui/material";

import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';




const containerStyle = {
    margin: '3rem auto',
    display: 'flex',
    justifyContent: 'center',
    
}

const links = {
    github: 'https://github.com/clarktr1',
    linkedIn: 'https://www.linkedin.com/in/trey-clark-695a281b6/'
}


function Footer() {
     
     const iconStyle = {
        fontSize: '3rem',
        margin: 'auto 2rem',
    }

    const links = [
        {   name: 'GitHub',
            href:'https://github.com/clarktr1',
            icon: GitHubIcon
        },
        {
            name: 'LinkedIn',
            href:'https://www.linkedin.com/in/trey-clark-695a281b6/',
            icon: LinkedInIcon
        },
      ]
      
      return (
        <Container style={containerStyle}>
          {links.map((link) => {
            return (
              <a href={link.href} target="_blank" key={link.name} className="links">
                {<link.icon style={iconStyle}></link.icon>}
              </a>
            );
          })}
        </Container>
      );
};

export default Footer;