import React from 'react';
import '../../styles.css'
import { ImageList, ImageListItem, Typography } from '@mui/material';
import project1 from '../../assets/project1.png'
import project2 from '../../assets/project2.png'
import project3 from '../../assets/project3.png'


const projects = [
    {
        title: 'Princess Saving Simulator',
        img: project1,
        link: 'https://github.com/clarktr1/Princess-Saving-Simulator'
    },
    {
        title: 'Classic Note Taker',
        img: project2,
        link: 'https://github.com/clarktr1/Express-Note-Taker'
    },
    {
        title: 'Express Tech Blog Website',
        img: project3,
        link: 'https://github.com/clarktr1/Blog-Site'
    },
]

const gridStyle = {
    width: '100vw',
    margin: '0 auto',
    padding: '2.5%',
}

export default function ProjectList(){
    
      return (
        <div style={gridStyle}  id="Projects">
          <Typography component='h2' className='center' sx={{fontSize: '5.8rem', color: 'gray'}}>My Works of Art</Typography>
    
          <ImageList
            sx={{ width: 700, height: 300}}
            variant="masonry"
            cols={3}
            gap={20}
            className='project-container'
          >
            {projects.map((project, index) => (
              <ImageListItem
                key={index}>
                <img
                  src={`${project.img}?w=161&fit=crop&auto=format`}
                  srcSet={project.img}
                  alt={project.title}
                  loading="eager"
                  className='listItem'
                />
                <div className="linkOverlay">
                  <a href={project.link} target='_blank' className="overlayLink">
                    <button>View Project</button>
                  </a>
                </div>
              </ImageListItem>
            ))}
          </ImageList>
        </div>
      );
    };