import React, { useState } from 'react';
import {
  Typography,
  TextField,
  Button,
  Grid,
  Paper,
  Container,
} from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';



const ContactForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(`Name: ${name}, Email: ${email}, Message: ${message}`);
  };

  const contactStyle = {
    margin: '4rem auto',
}
  return (
    <>
      <Container maxWidth='xl' style={contactStyle} id='contact'>
        <Paper style={{ padding: '3rem', maxWidth:'70vw', margin: 'auto'}} elevation={3}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={4} sx={{ padding: '3rem',}}>
              <Typography sx={{fontSize: '1.2rem'}}>Have questions? Want to work together? Want to know my favorite video game?</Typography>
              <Typography sx={{fontSize: '2.2rem', marginTop: '3rem'}}>Let's connect! </Typography>
            </Grid>
            <Grid item xs={12} md={8}>
            <form onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TextField
                      style={{ marginBottom: '10px' }}
                      label='Name'
                      variant='outlined'
                      fullWidth
                      value={name}
                      onChange={(event) => setName(event.target.value)}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      style={{ marginBottom: '10px' }}
                      label='Email'
                      variant='outlined'
                      fullWidth
                      value={email}
                      onChange={(event) => setEmail(event.target.value)}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      style={{ marginBottom: '10px' }}
                      label='Message'
                      variant='outlined'
                      multiline
                      rows={4}
                      fullWidth
                      value={message}
                      onChange={(event) => setMessage(event.target.value)}
                    />
                  </Grid>
                  <Grid item xs={12} sx={{display:'flex', justifyContent:'center'}}>
                    <Button variant='contained' color='primary' type='submit' >
                      Submit
                    </Button>
                  </Grid>
                </Grid>
              </form>
            </Grid>
          </Grid>

        </Paper>

      </Container>
 
  </>
  );
};

export default ContactForm;
